.item {
  margin-bottom: 30px;

  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

  width: calc((100% - 8 * 10px) / 4);
  margin: 10px;
  height: 150px;
}

.img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  object-fit: cover;
  cursor: pointer;
}

.img:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}
